
.dish-detail {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.dish-detail .top-section {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.dish-detail .top-section img {
      width: 300px;
      height: 300px;
}
.dish-detail .top-section p {
      position: absolute;
      width: 280px;
      height: 50px;
      line-height: 17px;
      top: 240px;
      font-size: 12px;
      padding-top: 5px;
      padding-bottom: 5px;
      padding-left: 10px;
      padding-right: 10px;
      text-overflow: ellipsis;
      overflow: hidden;
      color: white;
      background-color: rgba(29, 29, 29, 0.32);
      text-align: left;
}
.dish-detail .bottom-section {
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 90px;
}
.dish-detail .bottom-section .first-column {
      -webkit-box-flex: 1;
      -webkit-flex-grow: 1;
              flex-grow: 1;
      width: 100px;
      height: 100%;
}
.dish-detail .bottom-section .first-column img {
        width: 12px;
        height: 12px;
}
.dish-detail .bottom-section .first-column .title {
        -webkit-box-flex: 1;
        -webkit-flex-grow: 1;
                flex-grow: 1;
        font-size: 14px;
        margin-top: 5px;
        margin-left: 15px;
        width: 60%;
        text-align: left;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
}
.dish-detail .bottom-section .first-column .likes {
        width: 50px;
        font-size: 12px;
        margin-left: 15px;
        text-align: left;
        color: #747881;
}
.dish-detail .bottom-section .first-column .price-wrapper {
        color: #74b1f9;
        margin-left: 12px;
        text-align: left;
}
.dish-detail .bottom-section .first-column .price-wrapper .price {
          font-weight: bold;
          font-size: 20px;
}
.dish-detail .bottom-section .second-column {
      position: absolute;
      right: 10px;
      top: 89%;
}



