
.bussiness {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: 0;
  width: 100%;
  color: white;
}
.bussiness .bg {
    position: absolute;
    z-index: 0;
    background-color: #616161;
    height: 141px;
    width: 100%;
    opacity: 0.44;
}
.bussiness .first-section {
    position: relative;
    z-index: 20;
    padding-top: 20px;
    padding-bottom: 20px;
    margin-left: 10px;
    margin-right: 10px;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}
.bussiness .first-section button {
      -webkit-align-self: center;
              align-self: center;
      background-color: rgba(255, 255, 255, 0);
      border-color: white;
      border-width: 2px;
      border-style: solid;
      font-size: 15px;
      margin-right: 2px;
      color: white;
      width: 60px;
      height: 38px;
}
.bussiness .first-section .text {
      -webkit-box-flex: 1;
      -webkit-flex-grow: 1;
              flex-grow: 1;
      margin-left: 10px;
      width: 200px;
      display: -webkit-box;
      display: -webkit-flex;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
      -webkit-flex-direction: column;
              flex-direction: column;
}
.bussiness .first-section .text .name {
        font-size: 15px;
        text-overflow: ellipsis;
        overflow: hidden;
}
.bussiness .first-section .text .description {
        font-size: 12px;
        width: 200px;
        color: #e3e3e3;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
}
.bussiness .first-section img {
      height: 60px;
      width: 60px;
      border-color: white;
      border-radius: 5px;
}
.bussiness .second-section {
    position: relative;
    z-index: 20;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-size: 13px;
    margin-left: 10px;
    margin-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
}
.bussiness .second-section .introduction {
      -webkit-box-flex: 1;
      -webkit-flex-grow: 1;
              flex-grow: 1;
      width: 200px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
}
.bussiness .second-section .activity {
      width: 70px;
      -webkit-box-flex: 1;
      -webkit-flex-grow: 1;
              flex-grow: 1;
      text-align: right;
}
.bussiness div {
    width: 95%;
}
.bussiness .line {
    position: relative;
    z-index: 20;
    margin-left: 10px;
    margin-right: 10px;
    height: 1px;
    background-color: whitesmoke;
}



