
.menu {
  bottom: 46px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100%;
}
.menu .content {
    position: absolute;
    top: 140px;
    background-color: #f6f9ff;
    overflow: hidden;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 100%;
    height: 78%;
}
.menu .content ul, .menu .content li {
      margin-top: -5px;
      list-style-type: none;
}
.menu .content ul {
      background: #f6f9ff;
      margin-top: 1px;
}
.menu .content .category-wrapper {
      width: 120px;
      background: #f3f5f7;
      background-color: #f6f9ff;
      margin-top: 1px;
      overflow: hidden;
}
.menu .content .category-wrapper .category-item {
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-box-pack: start;
        -webkit-justify-content: flex-start;
                justify-content: flex-start;
        background-color: #ecf0f9;
        color: #777a83;
        width: 60px;
        height: 60px;
        border-radius: 5px;
        border-width: 1px;
        margin-top: 15px;
        margin-left: 15px;
}
.menu .content .category-wrapper .category-item .num {
          width: 55px;
          font-size: 25px;
          text-align: right;
          color: #ced1dc;
}
.menu .content .category-wrapper .category-item .text {
          width: 45px;
          margin-left: 6px;
          font-size: 14px;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
}
.menu .content .category-wrapper .category-item:after {
          position: absolute;
          content: '';
          left: 12px;
          width: 56px;
          bottom: 0;
}
.menu .content .category-wrapper .num-selected {
        width: 55px;
        font-size: 25px;
        text-align: right;
        color: #afd4fb;
}
.menu .content .category-wrapper .category-item-selected {
        background: #539ef9;
        color: white;
        box-shadow: 0px 8px 16px 0px #cae1fe;
}
.menu .content .dishes-wrapper {
      margin-top: 6px;
      overflow: hidden;
      width: 100%;
      margin-right: 15px;
      margin-left: 3px;
}
.menu .content .dishes-wrapper .dishes-ul::after {
        content: '';
        height: 40px;
        display: block;
}
.menu .content .dishes-wrapper .dish-item {
        height: 100px;
        width: 100%;
        margin-right: 15px;
        padding: 15px 8px 6px 6px;
        overflow: hidden;
        display: -webkit-box;
        display: -webkit-flex;
        display: flex;
}
.cart {
  height: 50px;
  width: 100%;
  position: absolute;
  bottom: 0px;
}



