
.cart-control {
  margin-right: 3px;
}
.cart-control .cart-count {
    position: relative;
    top: 3px;
    left: 2px;
    display: inline-block;
    width: 16px;
    color: #3a3b3b;
    text-align: center;
}
.cart-control .cart-minus, .cart-control .cart-plus {
    display: inline-block;
    vertical-align: middle;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    text-align: center;
    font-size: 20px;
    line-height: 15px;
}
.cart-control .cart-minus.fade-enter-active, .cart-control .cart-minus.fade-leave-active, .cart-control .cart-plus.fade-enter-active, .cart-control .cart-plus.fade-leave-active {
      -webkit-transition: all 0.3s linear;
      transition: all 0.3s linear;
}
.cart-control .cart-minus.fade-enter, .cart-control .cart-minus.fade-leave-active, .cart-control .cart-plus.fade-enter, .cart-control .cart-plus.fade-leave-active {
      opacity: 0;
      -webkit-transform: translate3d(24px, 0, 0);
              transform: translate3d(24px, 0, 0);
}
.cart-control .cart-plus {
    position: relative;
    z-index: 2;
}
.cart-control .cart-minus {
    position: relative;
    top: 0px;
    z-index: 1;
}



