
.dish-item-inner {
  overflow: hidden;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-flex: 1;
  -webkit-flex: auto;
          flex: auto;
  background-color: white;
  border-radius: 5px;
}
.dish-item-inner .left {
    -webkit-box-flex: 0;
    -webkit-flex: 0 0 100px;
            flex: 0 0 100px;
}
.dish-item-inner .left .dish-img {
      margin-top: 14px;
      margin-left: 14px;
      width: 84px;
      height: 70px;
}
.dish-item-inner .left .dish-img img {
        width: 100%;
        height: 100%;
        border-radius: 5%;
        border-color: white;
}
.dish-item-inner .right {
    padding-left: 10px;
    position: relative;
    overflow: hidden;
    -webkit-box-flex: 1;
    -webkit-flex: 1;
            flex: 1;
}
.dish-item-inner .right .dish-name {
      margin-top: 14px;
      font-size: 15px;
      height: 23px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: 60%;
      line-height: 17px;
}
.dish-item-inner .right .dish-description {
      font-size: 12px;
      color: #93999f;
      line-height: 17px;
      height: 23px;
      margin-top: 2px;
      margin-right: 3px;
      width: 80%;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
}
.dish-item-inner .right .price-wrapper {
      position: absolute;
      bottom: 10px;
      left: 10px;
      font-size: 12px;
      color: #74b1f9;
}
.dish-item-inner .right .price-wrapper .price {
        font-size: 16px;
}
.dish-item-inner .right .cart-control-wrapper {
      position: absolute;
      right: 14px;
      bottom: 10px;
}



